html {
    scroll-behavior: smooth;
}
.scroll-up__link {
    position: fixed;
    bottom: 5%;
    right: 5%;
    border-radius: 50%;
    background: linear-gradient(130deg, rgb(212, 20, 67, 0.2) 50%,  rgb(212, 20, 67, 0.5) 50%);
    background-size: 300%;
    width: 75px;
    height: 75px;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 12px;
    transition-duration: 250ms;
    transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
    &:hover,
    &:focus {
        background-position: 100% 0;
        // background: linear-gradient(130deg,rgb(212, 20, 67, 0.5) 50%,  rgb(212, 20, 67, 0.2) 50%);
    }
}

.scroll-up__icon {
    width: 40px;
    height: 40px;
    fill:#fff;
}